@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
*{margin:0;padding:0;font-family:"Roboto", sans-serif;font-weight:300;box-sizing:border-box}*::-webkit-scrollbar{width:10px}*::-webkit-scrollbar-track{background:#f1f1f1;border-radius:10px}*::-webkit-scrollbar-thumb{background:#888}*::-webkit-scrollbar-thumb:hover{background:#555}#root{min-height:100%;margin:0 auto;transition:0.2s;overflow:hidden;padding-bottom:50px;width:55vw}@media screen and (max-width: 1500px){#root{width:65vw !important}}@media screen and (max-width: 1100px){#root{width:75vw !important}}@media screen and (max-width: 900px){#root{width:80vw !important}}@media screen and (max-width: 700px){#root{width:100vw !important}}.hide{display:none}.clickable{transition:0.1s;cursor:pointer}.clickable:active{-webkit-transform:scale(0.97);transform:scale(0.97)}svg{transition:0.1s}.error{color:#f44336;margin-top:2px;font-weight:400;font-size:1rem}

